import { NextResponse } from 'next/server';
import pool from '../../../lib/db'; 

export async function GET(request, { params }) {
  try {
    const { id } = await params; // id à¹ƒà¸™à¸—à¸µà¹ˆà¸™à¸µà¹‰à¸„à¸·à¸­ emp_code (à¹€à¸Šà¹ˆà¸™ '106')

    // âœ… à¹à¸à¹‰à¹„à¸‚: à¸”à¸¶à¸‡à¸‚à¹‰à¸­à¸¡à¸¹à¸¥à¸ˆà¸²à¸à¸•à¸²à¸£à¸²à¸‡ attendance à¹‚à¸”à¸¢à¹ƒà¸Šà¹‰ emp_code à¹‚à¸”à¸¢à¸•à¸£à¸‡
    // (à¹ƒà¸«à¹‰à¸•à¸£à¸‡à¸à¸±à¸šà¸•à¸²à¸£à¸²à¸‡à¹ƒà¸™ Database à¸›à¸±à¸ˆà¸ˆà¸¸à¸šà¸±à¸™à¸‚à¸­à¸‡à¸„à¸¸à¸“)
    const sql = `
        SELECT * FROM attendance 
        WHERE emp_code = ? 
        ORDER BY work_date DESC 
        LIMIT 60
    `;
    const [rows] = await pool.query(sql, [id]);

    // à¸„à¸³à¸™à¸§à¸“à¸ªà¸–à¸´à¸•à¸´
    const stats = {
        present: rows.filter(r => r.status === 'Present').length,
        late: rows.filter(r => r.status === 'Late').length,
        absent: rows.filter(r => r.status === 'Absent').length,
        leave: rows.filter(r => r.status === 'Leave').length
    };

    return NextResponse.json({ history: rows, stats });

  } catch (error) {
    console.error("Fetch History Error:", error);
    return NextResponse.json({ error: error.message }, { status: 500 });
  }
}